.post {

  box-shadow: 0 1px 4px rgba(0, 0, 0, 0.39);

  text-align: center;
  position: relative;

}

.post a {

  color: black;

}

 

.image {

  width: 100%;

  max-height: 20rem;

  overflow: hidden;

}

.image img {

  object-fit: cover;

  width: 100%;

  height: 222px;

}

 

.content {

  padding: 1rem;

}

.content h3 {

  margin: 0.5rem 0;

  font-size: 1.5rem;

}

.content time {

  font-style: italic;

}

.content p {

  line-height: 1.5rem;

}
.remove-bookmark {

  position: absolute;

  cursor: pointer;

  bottom: 0.3rem;

  right: 0.2rem;

  width: 26px;

}

.remove-bookmark:hover {

  color: red;

}